@import url('https://fonts.googleapis.com/css2?family=Sarabun:wght@600&display=swap');

*{
  overflow-y: hidden;
}

.background{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.calculadora{
width: 20em;
height: 25em;
border-radius: 1.5em;
display: flex;
flex-direction: column;
justify-content: space-between;
border: 0.1em solid #777 ;
}

.screen{
  height: 17.5%;
  width: 90%;
  border-radius: 1em 1em .2em .2em;
  margin-top: 5%;
  margin-left: 5%;
  border: 0.1em solid #777 ;
  user-select: none;
}

.lectura{
  height: 35%;
  width: 90%;
  margin-left: 5%;
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  /* color: #222; */
  font-family: 'Sarabun', sans-serif;
}

.result{
  margin-left: 2%;
  width: 96%;
  height: 65%;
  display: flex;
  justify-content: end;
  align-items: flex-end;
  font-size: 1.5em;
  /* color: #222; */
  font-family: 'Sarabun', sans-serif;
}

.keys{
  margin: 2.5%;
  height: 75%;
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.key{
  font-size: 1em;
  width: 3.5em;
  height: 2em;
  margin: 0.4em;
  text-align: center;
  color:#444;
  border-radius: .4em;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.1em solid #777 ;
  cursor: pointer;
  font-family:Georgia, 'Times New Roman', Times, serif ;
  box-shadow: 0px 2px #555;
  user-select: none;
}

.key.arriba{
  width: 8.2em;
  /* color: #222; */
  
}

.toggles{
  position: absolute;
  display: flex;
  width: 100vw;
  height: 7vh;
  left: 0;
  top: 0;
}

.toggles_child{
  width: 33.333333333333%;
  height: 100%;
  /* border-left: .5em solid #222;
  border-bottom:.2em solid #222 ; */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
}
.toggles_child:last-child{
  /* border-right: .5em solid #222; */
}

.toggles_child:hover{background-color: #534b358f ;
}



.toggles_child h2{
  margin: 0;
  cursor: pointer;
}

#togg{
  border-left: none;
  border-right: none;
}